"use client";
import Link from "next/link";
import { useState, useEffect, useRef } from "react";
import { usePathname } from "next/navigation";
import {
	FaBars,
	FaTimes,
	FaSearch,
	FaShoppingCart,
	FaUser,
} from "react-icons/fa";
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import dynamic from "next/dynamic";

// Carregar useCart apenas no cliente
const useCart = () => {
	const [itemCount, setItemCount] = useState(0);

	useEffect(() => {
		// Simulação: buscar do localStorage
		const cart = JSON.parse(localStorage.getItem("cart") || "[]");
		setItemCount(
			cart.reduce((total: number, item: any) => total + item.quantity, 0)
		);
	}, []);

	return { itemCount };
};

export default function Header() {
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
	const [isSearchOpen, setIsSearchOpen] = useState(false);
	const pathname = usePathname();
	const headerRef = useRef<HTMLElement>(null);

	// Usar hook de carrinho apenas no cliente
	const { itemCount } = useCart();

	useEffect(() => {
		const handleScroll = () => {
			if (!headerRef.current) return;

			// Aplicar classe diretamente no elemento
			if (window.scrollY > 50) {
				headerRef.current.classList.add("scrolled");
				headerRef.current.classList.remove("not-scrolled");
			} else {
				headerRef.current.classList.remove("scrolled");
				headerRef.current.classList.add("not-scrolled");
			}
		};

		// Inicializar estado de scroll
		handleScroll();

		window.addEventListener("scroll", handleScroll);
		return () => window.removeEventListener("scroll", handleScroll);
	}, []);

	useEffect(() => {
		setIsMobileMenuOpen(false);
	}, [pathname]);

	const toggleSearch = () => {
		setIsSearchOpen(!isSearchOpen);
		setIsMobileMenuOpen(false);
	};

	return (
		<header
			ref={headerRef}
			className="fixed top-0 left-0 right-0 bg-[#F1F1F1] shadow-md w-full z-50 not-scrolled"
		>
			<div className="container mx-auto px-4 h-full">
				{isSearchOpen ? (
					<div className="flex items-center h-full">
						<button onClick={toggleSearch} className="mr-4 text-gray-800">
							<FaTimes className="text-xl" />
						</button>
						<Input
							autoFocus
							placeholder="Pesquise seu item"
							className="flex-1"
						/>
					</div>
				) : (
					<div className="flex justify-between items-center h-full">
						<div className="flex-1">
							<Link href={"/"}>
								<img
									src="https://in8.com.br/wp-content/themes/bootscore-child-main/img/logo.svg"
									alt="Logo da In8"
									width={100}
									height={40}
								/>
							</Link>
						</div>

						<nav className="hidden lg:flex flex-1 justify-center">
							<div className="flex space-x-8 items-center"></div>
						</nav>

						<div className="flex-1 flex justify-end">
							<div className="flex items-center gap-6">
								<button onClick={toggleSearch} className="text-gray-800">
									<FaSearch className="text-xl" />
								</button>

								<Link href={"/cart"} className="relative">
									<FaShoppingCart className="text-xl" />
									{/* Renderizar contagem apenas se maior que zero */}
									{itemCount > 0 && (
										<span className="absolute -top-2 -right-2 bg-red-600 text-white text-xs rounded-full h-5 w-5 flex items-center justify-center">
											{itemCount}
										</span>
									)}
								</Link>

								<DropdownMenu>
									<DropdownMenuTrigger className="text-gray-800">
										<FaUser className="text-xl" />
									</DropdownMenuTrigger>
									<DropdownMenuContent>
										<DropdownMenuSeparator />
										<DropdownMenuItem asChild>
											<Link href={"/user"}>Minha Conta</Link>
										</DropdownMenuItem>
									</DropdownMenuContent>
								</DropdownMenu>
							</div>
						</div>
					</div>
				)}
			</div>

			<style jsx>{`
				header.not-scrolled {
					height: 5rem;
				}
				header.scrolled {
					height: 4rem;
				}
				header {
					transition: height 0.3s ease;
				}
			`}</style>
		</header>
	);
}
